@import 'colors';

@font-face {
  font-family: 'Roboto';
  src: url('assets/Roboto-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: 'Roboto';
  src: url('assets/Roboto-Medium.ttf');
  font-weight: 500;
}

@font-face {
  font-family: 'Roboto';
  src: url('assets/Roboto-Bold.ttf');
  font-weight: 700;
}

@font-face {
  font-family: 'Circular Pro';
  src: url('assets/CircularPro-Book.otf');
  font-weight: 400;
}

@font-face {
  font-family: 'Circular Pro';
  src: url('assets/CircularPro-Medium.otf');
  font-weight: 500;
}

@font-face {
  font-family: 'Circular Pro';
  src: url('assets/CircularPro-Bold.otf');
  font-weight: 600;
}

@font-face {
  font-family: 'Circular Pro';
  src: url('assets/CircularPro-Black.otf');
  font-weight: 700;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: #D3D3D4;
  border-radius: 4px;
} 

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: left;
  display: flex;
  color: $fontColor;
  flex-direction: column;
  justify-content: left;
  font-size: 16px;
  line-height: 22px;
  background-color: white;

  .page-logo {
    width: 128px;
    height: auto;
    margin-top: 14px;
    margin-left: 60px;
  }
  .heading {
    color: $primaryColor;
    font-size: 50px;
    font-weight: 700;
    line-height: 57px;
  }
  .partial {
    opacity: 0.5;
  }
}
